// Themes
$primary: #121212;
$primary2: #5b6279;
$secondary: #2b67ff;
$accent: #f6f9ff;
$accent2: #fcfcfd;

$dark: #0a122a;
$dark-page: #0a122a;

$gm-logo: (
  color-1: #3867f6,
  color-2: #101010,
  color-3: #d2fd51,
);

$positive: #1976d2;
$negative: #d92d20;
$info: #00a761;
$warning: #fec84b;

/// Replace `$search` with `$replace` in `$string`
/// @author Hugo Giraudel
/// @param {String} $string - Initial string
/// @param {String} $search - Substring to replace
/// @param {String} $replace ('') - New value
/// @return {String} - Updated string
@function str-replace($string, $search, $replace: '') {
  $index: str-index($string, $search);
  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace +
      str-replace(
        str-slice($string, $index + str-length($search)),
        $search,
        $replace
      );
  }
  @return $string;
}

// Custom Variables
$placeholder-color: rgba($primary, 0.75);
$border-color: #e0e0e0;

$translucent: 0.3;
$transition-durations: (
  normal: 0.3s,
  md: 0.5s,
  lg: 0.7s,
  xl: 1s,
);

$generic-hover-transition: map-get($transition-durations, 'normal')
  cubic-bezier(0.25, 0.8, 0.5, 1);

$letter-spacing: (
  standard: -0.5px,
  normal: 0px,
  sm: 0.1rem,
  md: 0.2rem,
  lg: 0.3rem,
);

// Growmodo Extra Colors
$gmc-green: #bcf200;

$border: 8px;
$borders: (
  normal: 1px,
  md: 1.5px,
  lg: 2px,
  xl: 3px,
);

$active-bg: #ecfdf3;
$active-color: #027a48;
$negative-bg: #fef3f2;
$negative-color: #b42318;
$disable-bg: #f8f9fc;
$disable-color: #363f72;
$blue-bg: #eff8ff;
$blue-color: $secondary;
$warning-bg: #fffaeb;
$warning-color: #b54708;

$font-extra-light: 100;
